<template>
    <div class="custom-card">
        <div class="custom-card-header">
            {{this.$route.params.id ? $t('createNews.editTitle') : $t('createNews.title')}}
        </div>
        <div class="custom-card-content">
            <div class="form">
                <div class="input-wrapper" :class="{'errorInput' : errors.title}">
                    <label for="title">{{$t('createNews.titleText')}} <span>*</span></label>
                    <a-input id="title" v-model.trim="title" autoFocus :disabled="loading"/>
                    <div class="error" v-if="errors.title">{{$t('createNews.noTitle')}}</div>
                </div>
                <div class="input-wrapper" :class="{'errorInput' : errors.description}">
                    <label for="description">{{$t('createNews.description')}} <span>*</span></label>
                    <a-textarea
                            id="description"
                            :auto-size="{ minRows: 3 }"
                            v-model.trim="description"
                            :disabled="loading"
                    />
                    <div class="error" v-if="errors.description">{{$t('createNews.noDes')}}</div>
                </div>
                <div class="input-wrapper" v-if="this.$route.params.id">
                    <label for="status">{{$t('createNews.status')}} <span>*</span></label>
                    <a-select
                            id="status"
                            v-model="status"
                            @change="changeStatus"
                            style="width: 100%"
                            :disabled="loading"
                    >
                        <a-select-option
                                v-for="item in statusArray"
                                :key="item.value"
                        >
                            {{$t(item.label)}}
                        </a-select-option>
                    </a-select>
                </div>
                <a-button
                        type="submit"
                        class="custom-button"
                        @click="submitNews"
                        :disabled="loading"
                        :loading="loading"
                >
                    {{this.$route.params.id ? $t('button.edit') : $t('button.create')}}
                </a-button>
                <a-button class="custom-button cancel" :disabled="loading" v-if="!checkErr">
                    <router-link :to="linkList">{{$t('button.cancel')}}</router-link>
                </a-button>
                <a-button class="custom-button cancel" :disabled="loading" v-if="checkErr" @click="handleCancel">
                    {{$t('button.cancel')}}
                </a-button>
            </div>
        </div>
    </div>
</template>

<script>
    import data from "../../constant";
    import status from "../../constant/status";
    import links from "../../router/links";
    import i18n from "../../plugins/i18n";

    export default {
        name: "NewsForm",
        data() {
            return {
                title: '',
                description: '',
                status: '',
                statusArray: data.statusOptions,
                loading: false,
                errors: {title: '', description: ''},
                linkList: links.NEWS_LIST,
                date: null,
                checkErr: false,
                dataEdit: {},
            }
        },
        watch: {
            title() {
                if (this.errors.title) {
                    delete this.errors.title;
                }
                if (!this.$route.params.id) {
                    this.checkErr = !!this.title;
                }
                if (this.$route.params.id) {
                    this.checkErr = this.title !== this.dataEdit.title;
                }
            },
            description() {
                if (this.errors.description) {
                    delete this.errors.description;
                }
                if (!this.$route.params.id) {
                    this.checkErr = !!this.description;
                }
                if (this.$route.params.id) {
                    this.checkErr = this.description !== this.dataEdit.description;
                }
            }
        },
        mounted() {
            if (this.$route.params.id) {
                const id = parseInt(this.$route.params.id);
                let dataNews = this.$store.state.news.filter(item => item.key === id);
                this.dataEdit = dataNews[0];
                this.title = dataNews[0].title;
                this.description = dataNews[0].description;
                this.status = dataNews[0].status;
                this.date = dataNews[0].date;
            }
        },
        methods: {
            handleCancel() {
                const self = this;
                this.$confirm({
                    title: i18n.t('button.cancel'),
                    content: i18n.t('createNews.cancelText'),
                    okText: i18n.t('button.yes'),
                    okType: 'danger',
                    cancelText: i18n.t('button.no'),
                    onOk() {
                        self.$router.push({path: links.NEWS_LIST});
                    }
                });
            },
            handleError() {
                let checkError = false;
                if (!this.title) {
                    checkError = true;
                    this.errors.title = i18n.t('createNews.noTitle');
                }
                if (!this.description) {
                    checkError = true;
                    this.errors.description = i18n.t('createNews.noDes');
                }
                return checkError;
            },
            submitNews() {
                if (!this.handleError()) {
                    this.loading = true;
                    const id = parseInt(this.$route.params.id);
                    if (!id) {
                        const data = {
                            title: this.title,
                            description: this.description,
                            status: status.NEW,
                            key: this.$store.getters.indexLastItem + 1,
                            date: new Date(),
                        }
                        this.$store.dispatch('createNews', data);
                    } else {
                        let index = this.$store.state.news.findIndex(
                            item => item.key === id
                        );
                        this.$store.dispatch('deleteNews', index);
                        const data = {
                            title: this.title,
                            description: this.description,
                            status: this.status,
                            key: id,
                            date: this.date,
                        }
                        this.$store.dispatch('createNews', data);
                    }
                    setTimeout(() => {
                        const self = this;
                        this.$message.success(!id ? self.setMessage('new') : self.setMessage('edit'), 1);
                    }, 1000);
                    setTimeout(() => {
                        this.$router.push({path: links.NEWS_LIST});
                        this.loading = false;
                    }, 2000);
                }
            },
            changeStatus(value) {
                this.status = value;
            },
            setMessage(value) {
                const language = this.$store.state.language;
                let text = '';
                if (value === 'new') {
                    if (language === 'en') {
                        text = 'A news is created successfully!';
                    } else {
                        text = 'Tạo mới 1 tin tức thành công!';
                    }
                } else {
                    if (language === 'en') {
                        text = 'News is edited successfully!';
                    } else {
                        text = 'Cập nhật 1 tin tức thành công!';
                    }
                }
                return text;
            }
        }
    }
</script>

<style scoped lang="scss">
    .input-wrapper {
        margin-bottom: 10px;
        label {
            font-size: 0.9rem;
            margin-bottom: 3px;
            display: block;
            span {
                color: #ff0000;
            }
        }
        &.errorInput {
            input, textarea {
                border-color: #ff0000!important;
                &:focus {
                    box-shadow: 0 0 0 2px rgb(255 0 0 / 30%) !important;
                }
            }
        }
    }
</style>